	module.exports = {"data":{"markdownRemark":{"html":"<p>自分が初めて使った PC。\n親と共用で使える機会が滅多になかったが、殆ど一太郎 5 (DOS 版だぜ)しか使ってなかったので当時としてはあまり気にしていなかった。</p>\n<p>PC の価値というものを理解し始めたのが 1994 年中頃で、1995 ～ 1996 頃 辺りはちょっとごたごたしたが、その後<a href=\"/pc-9821\">自分専用の PC を導入し</a>どうでも良くなった。</p>\n<p>2003 年現在、現役機からは外れたものの、実家でたまに稼動中。</p>\n<p>2013 年現在、動態保存されている模様。ただし動くかどうか不明。</p>\n<h2>製作物・他やってたこと</h2>\n<ul>\n<li>(オリジナルゲーム)<a href=\"/6338\">滅びた城の宝 Ⅰ</a></li>\n<li>(オリジナルゲーム)<a href=\"/tag/cats-story\">CATS STORY</a> 製作開始(但し当時オツムが乏しく1年で頓挫)</li>\n<li>後は本当に一発ネタ的な文書作成位だなぁ</li>\n</ul>\n<h2>性能</h2>\n<ul>\n<li>CPU: intel486SX(20MHz)</li>\n<li>RAM: 5.6MB</li>\n<li>VGA: EGC</li>\n<li>VRAM: 256KB</li>\n<li>HDD: 40MB(SASI) + 176MB(SCSI)</li>\n<li>DRIVE: 5inch 2HD FDD ×2 + 3.5inch 2HD FDD ×1</li>\n<li>NET: 14.4kbpsModem</li>\n<li>SOUND: BEEP</li>\n<li>OS: MS-DOS 3.3C</li>\n</ul>","frontmatter":{"cover":null,"date":"1992-05-01T00:00:00+09:00","strDate":"1992/4/30","title":"(1992-1996) NEC PC-H98S model40"}}},"pathContext":{"path":"/ja/blog/1992/05/01-pc-h98s/","langKey":"ja"}}

